.card{
    height: 315px;
    width: 420px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: var(--color-White);
    border-radius: 10px;
    display: flex;
}

.desktop_img{
    width: 210px;
    border-top-left-radius:10px ;
    border-bottom-left-radius: 10px;
}

.mobile_img{
    
    height:147px
}

.content{
    /* background-color: aqua;     */
    width: 170px;
    height:315px;
    margin: 20px 20px;
    display: flex;
    flex-direction: column;
    
    
}

content{
    /* background-color: aqua;  */
    font-family: var(--font-Montserrat);
    color: var(--color-Darkgrayishblue);
    font-size: 10px;
    letter-spacing: 2px;
    margin-top: 0%;
    font-weight: 500;
}

h1{
    /* background-color: aqua;  */
    font-family: var(--font-Fraunces);
    font-size: 25px;
    line-height: 1;
    color: var(--color-Verydarkblue);
    margin-top: 10px;
}

.about{
    /* background-color: aqua;  */
    font-family: var(--font-Montserrat);
    color: var(--color-Darkgrayishblue);
    font-size: 10px;
    line-height: 1.6;
    margin-top: 0px;
    font-weight: 500;
}

.rate{

    font-family: var(--font-Fraunces);
    font-size: 22px;
    margin-top: 0px;
    color: var(--color-Darkcyan);
    font-weight: 700;
}

.rate s{
    font-family:var(--font-Montserrat) ;
    font-size: 10px;
    color: var(--color-Darkgrayishblue);
    margin-left: 15px;
    vertical-align: middle;
    font-weight: 500;
}

button img{
    width:10px;
    height: 13px;   
    margin-right: 8px;
    border-radius: 0px;
}
button{
    font-family:var(--font-Montserrat) ;
    color: white;
    height: 35px;
    vertical-align: middle;
    background-color: var(--color-Darkcyan);
    border-style: none;
    font-size: 10px;
    padding: 0;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
}

button:hover{
    background-color:var(--color-darkdarkcyan) ;
}


@media screen and (max-width:1440px) {
    .mobile_img{
        display:none;
    }
    
}

@media screen and (max-width:450px) {
    .mobile_img{
        display: inline;
        border-top-left-radius:10px ;
        border-top-right-radius: 10px;
        width: 250px;
    }
    .desktop_img{
        display: none;
    }
    .card{
        display: flex;
        flex-direction: column;
        width: 250px;
        min-height: 425px;
    }
    .content{
        width: 210px;
        
    }
    h1{
        font-size: 23px;
    }
    
}

